import React, { Component } from 'react';
import PropTypes from 'prop-types';
import BasicInput from '../BasicInput';
import { transformObjectToArray } from '../../utils/commonUtils';
import {
  INPUT_TYPE_MAP,
  TIP_LEVEL_MAP,
} from '../../config/propsPermittedValueMap';
import { INFO_INCOMPELETE, INPUT_PASSWORD_REGISTER } from '../../config/copywriting';

import styles from './index.module.scss';

const LEVEL_LIST = transformObjectToArray(TIP_LEVEL_MAP);
const INPUT_TYPE_LIST = transformObjectToArray(INPUT_TYPE_MAP);
const { DEFAULT, ERROR } = TIP_LEVEL_MAP;
const { PASSWORD } = INPUT_TYPE_MAP;

class LoginPasswordInput extends Component {
  static propTypes = {
    password: PropTypes.string,
    resetInput: PropTypes.bool,
    verifyTip: PropTypes.oneOfType([PropTypes.element, PropTypes.string]),
    tipLevel: PropTypes.oneOf(LEVEL_LIST),
    inputType: PropTypes.oneOf(INPUT_TYPE_LIST),
    onChange: PropTypes.func,
    onBlur: PropTypes.func,
    placeHolder: PropTypes.string,
  };

  static defaultProps = {
    password: '',
    resetInput: false,
    onChange: () => {},
    onBlur: () => {},
  };

  constructor(props) {
    super(props);
    this.state = {
      password: '',
      tipLevel: DEFAULT,
      verifyTip: '',
    };
    this.onFocus = this.onFocus.bind(this);
    this.onChange = this.onChange.bind(this);
  }

  componentWillReceiveProps(nextProps) {
    const { verifyTip, tipLevel, inputType, password, resetInput } = nextProps;
    if (inputType === PASSWORD) {
      this.setState({
        verifyTip,
        tipLevel,
      });
    }
    if (resetInput) {
      this.setState({
        password,
      });
    }
  }

  onFocus() {
    this.setState({
      tipLevel: DEFAULT,
      verifyTip: '',
    });
  }

  onChange(ev) {
    const {
      target: { value },
    } = ev;
    const { onChange } = this.props;
    this.setState({
      password: value,
    });
    onChange(ev);
  }

  verifyInput() {
    const { password } = this.state;
    if (!password) {
      this.setState({
        verifyTip: INFO_INCOMPELETE,
        tipLevel: ERROR,
      });
      return Promise.reject(new Error(INFO_INCOMPELETE));
    }
    return Promise.resolve(password);
  }

  render() {
    const { tipLevel, verifyTip, password } = this.state;
    const { placeHolder } = this.props;
    return (
      <div className={styles.loginPasswordInput}>
        <BasicInput
          placeHolder={placeHolder || INPUT_PASSWORD_REGISTER}
          tipLevel={tipLevel}
          value={password}
          inputType="password"
          onFocus={this.onFocus}
          onChange={this.onChange}
          onBlur={this.props.onBlur}
        >
          {verifyTip}
        </BasicInput>
      </div>
    );
  }
}

export default LoginPasswordInput;
