window.cly = function(eventCategory, eventAction, eventLabel) {
    window.Countly.q.push(['add_event',{
      "key": eventCategory,
      "segmentation": {
        eventAction,
        eventLabel,
      }
    }]);
}

const wxAccountCountly = (...args) => window.cly('WX_ACCOUNT_MYYXBJ', ...args);

export const ClyEnterCreateAccountPage = () => wxAccountCountly('Create_NewAccount_page', 'ShowPage');
