import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { post } from 'axios';
import { get as getValue } from 'lodash-es';
import jsCookie from 'js-cookie';
import LoginAccountInput from '../../components/LoginAccountInput';
import LoginPasswordInput from '../../components/LoginPasswordInput';
import BasicButton from '../../components/BasicButton';
// import ForgotPasswordLink from '../../components/ForgotPasswordLink';
import Separator from '../../components/Separator';
import DisableAccountTip from '../../components/DisableAccountTip';
import confirm from '../../components/Confirm/Confirm';

import {
  LOGIN_ACCOUNT_TO_BIND,
  ACCOUNT_ALREADY_BIND,
  WECHAT_ALREADY_BIND,
  PASSWORD_ERROR,
  COMMON_ERROR_TIPS,
  INVALID_WECHAT_SESSION,
  INPUT_PASSWORD,
  FORGOT_PASSWORD,
  DIRECT_CREATE_NEW_ACCOUNT,
  WX_BIND_ALREADY,
  CREATE_NEW_ACCOUNT,
  SWITCH_ACCOUNT,
} from '../../config/copywriting';
import {
  TIP_LEVEL_MAP,
  INPUT_TYPE_MAP,
} from '../../config/propsPermittedValueMap';
import {
  BIND_USER_BY_AUTH_TOKEN,
  VERIFY_CREDENTIAL,
} from '../../config/interfaceUrl';
import { LOGIN_URL } from '../../config/pageUrl';
import { loginAfterBind } from '../../utils/commonUtils';
import { setTwofactorInfo, saveBindLoginInfo } from '../../store/actions';
import { 
  ClyEnterBindExistedPage,
  ClyEnterAccount,
  ClyEnterExistedPassword,
  ClyClickContinue,
  ClyClickForgetPassword,
  ClyClickCreateNewAccount,
} from '../../countly/countlyPoints';

import styles from './index.module.scss';

const { DEFAULT, ERROR } = TIP_LEVEL_MAP;
const { ACCOUNT, PASSWORD, DEFAULT: INPUT_DEFAULT } = INPUT_TYPE_MAP;

class BindExisted extends Component {
  static propTypes = {
    wechatSessionId: PropTypes.string.isRequired,
    setTwofactorInfo: PropTypes.func.isRequired,
    bindLoginInfo: PropTypes.object.isRequired,
    saveBindLoginInfo: PropTypes.func.isRequired,
    history: PropTypes.object.isRequired,
  };

  constructor(props) {
    super(props);
    this.state = {
      account: '',
      accountTipLevel: DEFAULT,
      accountVerifyTip: '',
      password: '',
      passwordTipLevel: DEFAULT,
      passwordVerifyTip: '',
      inputType: INPUT_DEFAULT,
      requesting: false,
      resetInput: false,
    };
    this.goToBind = this.goToBind.bind(this);
    this.onPasswordInputChange = this.onPasswordInputChange.bind(this);
    this.onAccountInputChange = this.onAccountInputChange.bind(this);
  }

  componentDidMount() {
    const {
      bindLoginInfo: { account, password },
    } = this.props;
    this.setState(
      {
        account,
        password,
        resetInput: true,
      },
      () => {
        this.setState({
          resetInput: false,
        });
      },
    );
    ClyEnterBindExistedPage();
  }

  componentWillUnmount() {
    const { account, password } = this.state;
    const { saveBindLoginInfo: _saveBindLoginInfo } = this.props;
    _saveBindLoginInfo({
      account,
      password,
    });
  }

  onAccountInputChange({ target: { value } }) {
    this.setState({
      account: value,
    });
  }

  onPasswordInputChange({ target: { value } }) {
    this.setState({
      password: value,
    });
  }

  forgotPassword = () => {
    ClyClickForgetPassword();
    this.props.history.push('/forget-password')
  }


  redirectToCreate = () => {
    ClyClickCreateNewAccount()
    window.wx.closeWindow();
    this.props.history.push('/bind-create');
  }

  goToBind() {
    let redirectTo = '';
    let verifyResponseContent;
    const {
      history,
      setTwofactorInfo: _setTwofactorInfo,
      wechatSessionId,
    } = this.props;
    ClyClickContinue();
    this.setState({
      requesting: true,
    });
    Promise.all([
      this.refs.accountInput.verifyInput(),
      this.refs.passwordInput.verifyInput(),
    ])
      .then(([account, password]) => {
        jsCookie.set('registrationSpeechAccount', account, {
          expires: 1 / 24 / 60,
        });
        return post(
          VERIFY_CREDENTIAL,
          {
            identity: account,
            password,
          },
          {
            needSignature: true,
          },
        );
      })
      .then(data => {
        const code = getValue(data, 'data.code');
        const authToken = getValue(data, 'data.content.authToken');
        const twoFactorEnabled = getValue(
          data,
          'data.content.twoFactorEnabled',
        );
        const twoFactorHint = getValue(data, 'data.content.twoFactorHint');
        let success;
        verifyResponseContent = getValue(data, 'data.content');
        switch (code) {
          case 200:
            if (twoFactorEnabled) {
              _setTwofactorInfo({
                twoFactorEnabled,
                twoFactorHint,
                twoFactorToken: authToken,
                fromWechatLogin: false,
              });
              success = false;
              redirectTo = '/twofactor-verification';
            } else {
              success = !!authToken; // 确保接口的确返回了 authToken, 有 authToken 才算成功
            }
            break;
          case 403:
            this.updateVerifyInfo(PASSWORD, {
              tipLevel: ERROR,
              verifyTip: PASSWORD_ERROR,
            });
            success = false;
            break;
          default:
            success = false;
            this.updateVerifyInfo(PASSWORD, {
              tipLevel: ERROR,
              verifyTip: PASSWORD_ERROR,
            });
        }
        if (success) {
          return authToken;
        }
        return Promise.reject();
      })
      .then(authToken => {
        return post(
          BIND_USER_BY_AUTH_TOKEN,
          {
            authToken,
            sessionId: wechatSessionId,
          },
          {
            needSignature: true,
          },
        );
      })
      .then(data => {
        const code = getValue(data, 'data.code');
        let success;
        switch (code) {
          case 200:
            success = true;
            break;
          case 404:
            success = true;
            confirm({ content:INVALID_WECHAT_SESSION });
            break;
          case 423:
            success = false;
            this.updateVerifyInfo(ACCOUNT, {
              tipLevel: ERROR,
              verifyTip: ACCOUNT_ALREADY_BIND,
            });
            break;
          case 425:
            success = false;
            this.bindAready();
            confirm({ content: WECHAT_ALREADY_BIND });
            break;
          default:
            success = false;
            confirm({ content: COMMON_ERROR_TIPS });
        }
        if (success) {
          return verifyResponseContent;
        }
        return Promise.reject();
      })
      .then(({ authToken, hpts, hsHash }) => {
        // loginAfterBind({
        //   action: LOGIN_URL,
        //   authToken,
        //   hpts,
        //   hsHash,
        // });
      })
      .catch(err => {
        console.log(err);
      })
      .finally(() => {
        if (!redirectTo) {
          // 防止在跳转后组件已经卸载但是仍然更新 state 导致内存泄露
          this.setState({
            inputType: INPUT_DEFAULT,
            requesting: false,
          });
        } else {
          history.push(redirectTo);
        }
      });
  }

  bindAready = () => {
    confirm({
      content: WX_BIND_ALREADY,
      operates: [{
        text: CREATE_NEW_ACCOUNT,
        main: true,
        onClick: () => this.props.history.push('/bind-create'),
      },
      {
        text: SWITCH_ACCOUNT,
        main: true,
        onClick: () => {
          this.setState({
            account: '',
            password: '',
            resetInput: true,
          }, 
          () => {
            this.setState({
              resetInput: false,
            })
          })
        },
      }]
    })
  }

  updateVerifyInfo(inputType, { tipLevel, verifyTip }) {
    const verifyTipKey = `${inputType}VerifyTip`;
    const tipLevelKey = `${inputType}TipLevel`;
    this.setState({
      inputType,
      [verifyTipKey]: verifyTip,
      [tipLevelKey]: tipLevel,
    });
  }

  render() {
    const {
      account,
      password,
      accountTipLevel,
      accountVerifyTip,
      passwordTipLevel,
      passwordVerifyTip,
      requesting,
      inputType,
      resetInput,
    } = this.state;
    const accountWasDisableTip = <DisableAccountTip account={account} />;
    return (
      <div className={styles.bindExisted}>
        <div className={styles.accountInputWrapper}>
          <LoginAccountInput
            ref="accountInput"
            tipLevel={accountTipLevel}
            verifyTip={accountVerifyTip}
            inputType={inputType}
            account={account}
            accountWasDisableTip={accountWasDisableTip}
            onChange={this.onAccountInputChange}
            onBlur={ClyEnterAccount}
            resetInput={resetInput}
          />
        </div>
        <div className={styles.passwordInputWrapper}>
          <LoginPasswordInput
            ref="passwordInput"
            tipLevel={passwordTipLevel}
            verifyTip={passwordVerifyTip}
            inputType={inputType}
            password={password}
            onChange={this.onPasswordInputChange}
            onBlur={ClyEnterExistedPassword}
            resetInput={resetInput}
            placeHolder={INPUT_PASSWORD}
          />
        </div>
        <div className={styles.buttonWrapper}>
          <BasicButton
            text={LOGIN_ACCOUNT_TO_BIND}
            onClick={this.goToBind}
            loading={requesting}
          />
        </div>
        <div className={styles.linkWrapper}>
          <BasicButton text={FORGOT_PASSWORD}
                       buttonWrapperClass={styles.linkWrapper}
                       onClick={this.forgotPassword}
          />
        </div>
        <Separator />
        <BasicButton
          text={DIRECT_CREATE_NEW_ACCOUNT}
          buttonWrapperClass={styles.bindCreateBtn}
          onClick={this.redirectToCreate}
          loading={requesting}
        />
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    wechatSessionId: state.wechatSessionId,
    bindLoginInfo: state.bindLoginInfo,
  };
};
const mapDispatchToProps = {
  setTwofactorInfo: info => {
    return setTwofactorInfo(info);
  },
  saveBindLoginInfo: info => {
    return saveBindLoginInfo(info);
  },
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(BindExisted);
